export default {
	data() {
		return {
			title: 'Hello',
			list:[]
		}
	},
	onLoad() {
		// 通过showLoading可以为页面设置加载动画
		uni.showLoading({
			title:"加载中......"
		}),
		this.getNews()
	},
	methods: {
		async getNews(callback){
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news',
				method: 'GET',
				data: {},
				success: res => {
					// console.log(res);
					this.list = res.data;
					uni.hideLoading();
				},
				fail: () => {},
				complete: () => {}
			});
			callback && callback()
		},
		openinfo(e){
			// console.log(e);
			var listid = e.currentTarget.dataset.listsid;
			// console.log(listid)
			uni.navigateTo({
				url: '../info/info?newsid='+listid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onPullDownRefresh() {
		console.log("下拉刷新了");
		this.list = []
		setTimeout(()=>{
			// this.getNews();
			// uni.stopPullDownRefresh()
			this.getNews(()=>{
				uni.stopPullDownRefresh()
			})
		}, 1000);
	}
}
